
body[data-bs-theme="light"] {
  --bs-body-bg: #fec7d7;
  --bs-heading-color: #0e172c;
  --bs-btn-bg: #fec7d7;
  --bs-tertiary-bg-rgb: 167, 134, 223;
  --bs-link-color-rgb: 14, 23, 44;
  --bs-link-hover-color-rgb: 167, 134, 223;
}

body[data-bs-theme="light"] h1 a:hover {
  color: var(--bs-heading-color);
}

body[data-bs-theme="light"] .bg-body-tertiary a:hover {
  color: var(--bs-body-bg);
  text-decoration: underline !important;
}

body[data-bs-theme="light"] a.btn-outline-light:hover {
  color: var(--bs-heading-color) !important;
}